import React from "react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  MessageCircleQuestion,
  MoreHorizontal,
  Calendar,
  Clock,
  MessageSquare,
  Tag,
} from "lucide-react";
import { SupportTicket } from "@/api/services/support/supportService";

interface Action {
  label: string;
  icon: React.ReactNode;
  onClick: (item: SupportTicket) => void;
  variant?: "default" | "destructive";
}

interface SupportTicketGridProps {
  data: SupportTicket[];
  onItemClick: (item: SupportTicket) => void;
  actions: Action[];
  getStatusBadgeVariant: (status: string) => string;
  getPriorityBadgeVariant: (priority: string) => string;
  getCategoryBadgeVariant: (category: string) => string;
  getStatusIcon: (status: string) => React.ReactNode;
  formatDate: (dateString: string) => string;
  isSuperAdmin?: boolean;
  onStatusUpdate?: (ticketId: string, status: string) => void;
  isUpdatingStatus?: boolean;
}

const SupportTicketGrid: React.FC<SupportTicketGridProps> = ({
  data,
  onItemClick,
  actions,
  getStatusBadgeVariant,
  getPriorityBadgeVariant,
  getCategoryBadgeVariant,
  getStatusIcon,
  formatDate,
  isSuperAdmin = false,
  onStatusUpdate,
  isUpdatingStatus = false,
}) => {
  const truncateText = (text: string, maxLength: number) => {
    if (text.length <= maxLength) return text;
    return text.substring(0, maxLength) + "...";
  };

  const formatTicketId = (ticketId: string) => {
    return `#${ticketId.slice(-8).toUpperCase()}`;
  };

  // Status dropdown component for super admin
  const StatusDropdown = ({ ticket }: { ticket: SupportTicket }) => {
    const getAvailableStatuses = (currentStatus: string) => {
      const allStatuses = [
        { value: "open", label: "Open", disabled: false },
        { value: "in_progress", label: "Under Process", disabled: false },
        { value: "waiting_response", label: "Waiting Response", disabled: false },
        { value: "resolved", label: "Complete", disabled: false },
        { value: "closed", label: "Closed", disabled: false },
      ];

      // Filter out current status from dropdown options
      return allStatuses.filter(status => status.value !== currentStatus);
    };

    return (
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <div className="cursor-pointer" onClick={(e) => e.stopPropagation()}>
            <Badge variant={getStatusBadgeVariant(ticket.status) as any} className="flex items-center gap-1">
              {getStatusIcon(ticket.status)}
              {ticket.status === "in_progress" ? "Under Process" :
               ticket.status === "resolved" ? "Complete" :
               ticket.status.replace('_', ' ')}
            </Badge>
          </div>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start" className="w-48">
          {getAvailableStatuses(ticket.status).map((status) => (
            <DropdownMenuItem
              key={status.value}
              onClick={(e) => {
                e.stopPropagation();
                onStatusUpdate?.(ticket.ticket_id, status.value);
              }}
              disabled={isUpdatingStatus}
              className="cursor-pointer"
            >
              <div className="flex items-center gap-2">
                {getStatusIcon(status.value)}
                <span>{status.label}</span>
              </div>
            </DropdownMenuItem>
          ))}
        </DropdownMenuContent>
      </DropdownMenu>
    );
  };

  if (data.length === 0) {
    return (
      <div className="text-center p-8">
        <div className="h-16 w-16 bg-primary/10 rounded-full flex items-center justify-center mx-auto mb-4">
          <MessageCircleQuestion className="h-8 w-8 text-primary" />
        </div>
        <h3 className="text-lg font-medium mb-1">No support tickets found</h3>
        <p className="text-gray-500 mb-4">
          Get started by creating your first support ticket
        </p>
      </div>
    );
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {data.map((ticket) => (
        <div
          key={ticket.id}
          className="bg-card border border-border/50 rounded-lg p-6 hover:shadow-md transition-all duration-200 cursor-pointer group"
          onClick={() => onItemClick(ticket)}
        >
          {/* Header */}
          <div className="flex items-start justify-between mb-4">
            <div className="flex items-center space-x-3">
              <div className="h-10 w-10 bg-gradient-to-br from-primary/20 to-primary/10 rounded-lg flex items-center justify-center">
                <MessageCircleQuestion className="h-5 w-5 text-primary" />
              </div>
              <div>
                <h3 className="font-semibold text-foreground line-clamp-1">
                  {truncateText(ticket.title, 30)}
                </h3>
                <p className="text-xs text-muted-foreground">
                  {formatTicketId(ticket.ticket_id)}
                </p>
              </div>
            </div>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button
                  variant="ghost"
                  size="sm"
                  className="h-8 w-8 p-0 opacity-0 group-hover:opacity-100 transition-opacity"
                  onClick={(e) => e.stopPropagation()}
                >
                  <MoreHorizontal className="h-4 w-4" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-48">
                {actions.map((action, index) => (
                  <DropdownMenuItem
                    key={index}
                    onClick={(e) => {
                      e.stopPropagation();
                      action.onClick(ticket);
                    }}
                    className={
                      action.variant === "destructive"
                        ? "text-red-600 focus:text-red-600"
                        : ""
                    }
                  >
                    {action.icon}
                    <span className="ml-2">{action.label}</span>
                  </DropdownMenuItem>
                ))}
              </DropdownMenuContent>
            </DropdownMenu>
          </div>

          {/* Description */}
          <p className="text-sm text-muted-foreground mb-4 line-clamp-2">
            {truncateText(ticket.description, 100)}
          </p>

          {/* Status and Priority Badges */}
          <div className="flex items-center space-x-2 mb-4">
            {isSuperAdmin ? (
              <StatusDropdown ticket={ticket} />
            ) : (
              <Badge variant={getStatusBadgeVariant(ticket.status) as any} className="flex items-center gap-1">
                {getStatusIcon(ticket.status)}
                {ticket.status === "in_progress" ? "Under Process" :
                 ticket.status === "resolved" ? "Complete" :
                 ticket.status.replace('_', ' ')}
              </Badge>
            )}
            <Badge variant={getPriorityBadgeVariant(ticket.priority) as any}>
              {ticket.priority.toUpperCase()}
            </Badge>
            <Badge variant={getCategoryBadgeVariant(ticket.category) as any}>
              {ticket.category.replace('_', ' ')}
            </Badge>
          </div>

          {/* Tags */}
          {ticket.tags && ticket.tags.length > 0 && (
            <div className="flex items-center space-x-1 mb-4">
              <Tag className="h-3 w-3 text-muted-foreground" />
              <div className="flex flex-wrap gap-1">
                {ticket.tags.slice(0, 2).map((tag, index) => (
                  <span
                    key={index}
                    className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-secondary text-secondary-foreground"
                  >
                    {tag}
                  </span>
                ))}
                {ticket.tags.length > 2 && (
                  <span className="text-xs text-muted-foreground">
                    +{ticket.tags.length - 2} more
                  </span>
                )}
              </div>
            </div>
          )}

          {/* Footer */}
          <div className="flex items-center justify-between text-xs text-muted-foreground pt-4 border-t border-border/50">
            <div className="flex items-center space-x-4">
              <div className="flex items-center space-x-1">
                <MessageSquare className="h-3 w-3" />
                <span>{ticket.message_count}</span>
              </div>
              <div className="flex items-center space-x-1">
                <Calendar className="h-3 w-3" />
                <span>{formatDate(ticket.created_at)}</span>
              </div>
            </div>
            <div className="flex items-center space-x-1">
              <Clock className="h-3 w-3" />
              <span>{formatDate(ticket.updated_at)}</span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default SupportTicketGrid;
