import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ModernTable } from "@/components/ModernTable";
import Header from "@/components/Header";
import Page from "@/components/Page";
import {
  Edit,
  Eye,
  Trash,
  Plus,
  MessageCircleQuestion,
  Clock,
  CheckCircle,
  XCircle,
  AlertCircle,
  Calendar,
  MessageSquare,
  Circle,
} from "lucide-react";
import { AddSupportTicket } from "./AddSupportTicket";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { getAllSupportTickets, deleteSupportTicket, updateSupportTicket, SupportTicket } from "@/api/services/support/supportService";
import { useSearchParams } from "react-router-dom";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import SupportTicketGrid from "@/components/SupportTicketGrid";
import SupportFilterPopover from "@/components/SupportFilterPopover";
import { toast } from "sonner";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";

import { useAuth } from "@/context/AuthContext";

// API Response interface
interface SupportTicketsResponse {
  success: boolean;
  message: string;
  data: SupportTicket[];
  count: number;
  total: number;
  is_super_admin: boolean;
}

const Support = () => {
  const navigate = useNavigate();
  const [searchParams, setSearchParams] = useSearchParams();
  const [debouncedSearch, setDebouncedSearch] = useState(
    searchParams.get("search") || ""
  );
  const [searchQuery, setSearchQuery] = useState(debouncedSearch);
  const [viewMode, setViewMode] = useState<"list" | "grid">(
    searchParams.get("view") === "grid" ? "grid" : "list"
  );
  const [activeFilters, setActiveFilters] = useState<any>({});
  const [deleteTicketId, setDeleteTicketId] = useState<string | null>(null);
  const [isPerformingDemoLogin, setIsPerformingDemoLogin] = useState(false);
  const [hasTriedAutoLogin, setHasTriedAutoLogin] = useState(false);

  const queryClient = useQueryClient();
  const { login, isLoggedIn } = useAuth();



  // Extract current page from searchParams
  const currentPage = parseInt(searchParams.get("page") || "1");
  const itemsPerPage = 10;

  // Auto-login effect - try demo login if not authenticated
  useEffect(() => {
    if (!isLoggedIn && !hasTriedAutoLogin && !isPerformingDemoLogin) {
      setHasTriedAutoLogin(true);
      
    }
  }, [isLoggedIn, hasTriedAutoLogin, isPerformingDemoLogin]);

  // Debounce search input
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearch(searchQuery);
    }, 300);

    return () => clearTimeout(timer);
  }, [searchQuery]);

  // Update URL when search changes
  useEffect(() => {
    const newParams = new URLSearchParams(searchParams);
    if (debouncedSearch) {
      newParams.set("search", debouncedSearch);
    } else {
      newParams.delete("search");
    }
    newParams.set("page", "1"); // Reset to first page when searching
    setSearchParams(newParams);
  }, [debouncedSearch, setSearchParams]);

  // Fetch support tickets
  const {
    data: ticketsData,
    isLoading,
    error,
    refetch,
  } = useQuery<SupportTicketsResponse>({
    queryKey: ["supportTickets", debouncedSearch, activeFilters, currentPage],
    queryFn: () => getAllSupportTickets({
      search: debouncedSearch,
      ...activeFilters,
      page: currentPage,
      limit: itemsPerPage,
    }) as Promise<SupportTicketsResponse>,
    staleTime: 0, // Always consider data stale for immediate updates
    gcTime: 5 * 60 * 1000, // Keep in cache for 5 minutes
    placeholderData: (previousData) => previousData, // Keep previous data while loading
  });

  // Delete mutation
  const deleteMutation = useMutation({
    mutationFn: deleteSupportTicket,
    onSuccess: () => {
      toast.success("Support ticket deleted successfully");
      queryClient.invalidateQueries({ queryKey: ["supportTickets"] });
      setDeleteTicketId(null);
    },
    onError: (error: any) => {
      toast.error(error.message || "Failed to delete support ticket");
      setDeleteTicketId(null);
    },
  });

  // Update status mutation with optimistic updates
  const updateStatusMutation = useMutation({
    mutationFn: ({ ticketId, status }: { ticketId: string; status: string }) =>
      updateSupportTicket(ticketId, { status }),
    onMutate: async ({ ticketId, status }) => {
      console.log(`ðŸ”„ Starting optimistic update for ticket ${ticketId} to status ${status}`);

      // Cancel any outgoing refetches
      await queryClient.cancelQueries({ queryKey: ["supportTickets"] });

      // Snapshot the previous value for rollback
      const previousData = queryClient.getQueriesData({ queryKey: ["supportTickets"] });

      // Get all current queries to see what we're working with
      const allQueries = queryClient.getQueriesData({ queryKey: ["supportTickets"] });
      console.log("ðŸ” All support ticket queries:", allQueries.map(([key]) => key));

      // Update all support ticket queries optimistically
      queryClient.setQueriesData(
        { queryKey: ["supportTickets"] },
        (old: any) => {
          console.log("ðŸ” Processing query data:", {
            hasData: !!old?.data,
            dataLength: old?.data?.length,
            dataType: typeof old?.data
          });

          if (!old?.data) {
            console.log("âŒ No data found for optimistic update");
            return old;
          }

          // Find the ticket to update
          const ticketIndex = old.data.findIndex((ticket: SupportTicket) => ticket.ticket_id === ticketId);
          console.log(`ðŸŽ¯ Found ticket at index ${ticketIndex} for ID ${ticketId}`);

          if (ticketIndex === -1) {
            console.log("âŒ Ticket not found in current data");
            return old;
          }

          console.log(`âœ… Optimistically updating ticket ${ticketId} status from ${old.data[ticketIndex].status} to ${status}`);

          const updatedData = {
            ...old,
            data: old.data.map((ticket: SupportTicket) =>
              ticket.ticket_id === ticketId
                ? {
                    ...ticket,
                    status,
                    updated_at: new Date().toISOString()
                  }
                : ticket
            ),
          };

          console.log("ðŸ“Š Updated ticket:", updatedData.data.find((t: SupportTicket) => t.ticket_id === ticketId));

          return updatedData;
        }
      );

      return { previousData };
    },
    onSuccess: (data, variables) => {
      console.log(`âœ… Status update successful for ticket ${variables.ticketId}`);
      toast.success("Ticket status updated successfully");

      // Scenario 1: Auto-refresh the list immediately
      queryClient.invalidateQueries({ queryKey: ["supportTickets"] });

      // Force refetch to ensure fresh data
      refetch();

      // Scenario 2: Navigate to Support page after status update
      setTimeout(() => {
        navigate("/support");
      }, 1500); // Small delay to show the success message and refresh
    },
    onError: (error: any, variables, context) => {
      console.log(`âŒ Status update failed for ticket ${variables.ticketId}:`, error);
      toast.error(error.message || "Failed to update ticket status");

      // Rollback optimistic updates
      if (context?.previousData) {
        context.previousData.forEach(([queryKey, data]) => {
          queryClient.setQueryData(queryKey, data);
        });
      }
    },
    onSettled: () => {
      // Always refetch to ensure consistency
      console.log("ðŸ Mutation settled - refreshing data");
      queryClient.invalidateQueries({ queryKey: ["supportTickets"] });
      refetch();
    },
  });

  const tickets = ticketsData?.data || [];
  const isSuperAdmin = ticketsData?.is_super_admin || false;

  const handleSearchChange = (value: string) => {
    setSearchQuery(value);
  };

  const handleViewModeChange = (mode: "list" | "grid") => {
    setViewMode(mode);
    const newParams = new URLSearchParams(searchParams);
    newParams.set("view", mode);
    setSearchParams(newParams);
  };

  const handleFilterChange = (filters: any) => {
    setActiveFilters(filters);
    const newParams = new URLSearchParams(searchParams);
    newParams.set("page", "1"); // Reset to first page when filtering
    setSearchParams(newParams);
  };

  const handleDeleteTicket = (ticketId: string) => {
    setDeleteTicketId(ticketId);
  };

  const handleStatusUpdate = (ticketId: string, newStatus: string) => {
    console.log(`ðŸš€ Initiating status update for ticket ${ticketId} to ${newStatus}`);

    // Start the mutation
    updateStatusMutation.mutate({ ticketId, status: newStatus });

    // Also trigger an immediate refresh to ensure data is fresh
    setTimeout(() => {
      console.log("ðŸ”„ Triggering immediate refresh after status update");
      refetch();
    }, 500);
  };

  const confirmDelete = () => {
    if (deleteTicketId) {
      deleteMutation.mutate(deleteTicketId);
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
  };

  // Status display component - Role-based functionality
  const StatusDisplay = ({ ticket }: { ticket: SupportTicket }) => {
    // Regular users see read-only status badge
    if (!isSuperAdmin) {
      return (
        <Badge variant={getStatusBadgeVariant(ticket.status)} className="flex items-center gap-1">
          {getStatusIcon(ticket.status)}
          {ticket.status === "in_progress" ? "Under Process" :
           ticket.status === "resolved" ? "Complete" :
           ticket.status.replace('_', ' ')}
        </Badge>
      );
    }

    // Super admin gets dropdown to change status
    const getAvailableStatuses = (currentStatus: string) => {
      const allStatuses = [
        { value: "open", label: "Open", disabled: false },
        { value: "in_progress", label: "Under Process", disabled: false },
        { value: "waiting_response", label: "Waiting Response", disabled: false },
        { value: "resolved", label: "Complete", disabled: false },
        { value: "closed", label: "Closed", disabled: false },
      ];

      // Filter out current status from dropdown options
      return allStatuses.filter(status => status.value !== currentStatus);
    };

    return (
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <div className="cursor-pointer" onClick={(e) => e.stopPropagation()}>
            <Badge variant={getStatusBadgeVariant(ticket.status)} className="flex items-center gap-1">
              {getStatusIcon(ticket.status)}
              {ticket.status === "in_progress" ? "Under Process" :
               ticket.status === "resolved" ? "Complete" :
               ticket.status.replace('_', ' ')}
            </Badge>
          </div>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="start" className="w-48">
          {getAvailableStatuses(ticket.status).map((status) => (
            <DropdownMenuItem
              key={status.value}
              onClick={(e) => {
                e.stopPropagation();
                handleStatusUpdate(ticket.ticket_id, status.value);
              }}
              disabled={updateStatusMutation.isPending}
              className="cursor-pointer"
            >
              <div className="flex items-center gap-2">
                {getStatusIcon(status.value)}
                <span>{status.label}</span>
              </div>
            </DropdownMenuItem>
          ))}
        </DropdownMenuContent>
      </DropdownMenu>
    );
  };

  const getStatusBadgeVariant = (status: string) => {
    switch (status) {
      case "open":
        return "default";
      case "in_progress":
        return "secondary";
      case "waiting_response":
        return "outline";
      case "resolved":
        return "success";
      case "closed":
        return "destructive";
      default:
        return "default";
    }
  };

  const getPriorityBadgeVariant = (priority: string) => {
    switch (priority) {
      case "low":
        return "secondary";
      case "medium":
        return "default";
      case "high":
        return "destructive";
      case "urgent":
        return "destructive";
      default:
        return "default";
    }
  };

  const getCategoryBadgeVariant = (category: string) => {
    switch (category) {
      case "technical":
        return "default";
      case "billing":
        return "secondary";
      case "feature_request":
        return "outline";
      case "bug_report":
        return "destructive";
      case "general":
        return "secondary";
      default:
        return "default";
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case "open":
        return <Circle className="h-3 w-3" />;
      case "in_progress":
        return <Clock className="h-3 w-3" />;
      case "waiting_response":
        return <AlertCircle className="h-3 w-3" />;
      case "resolved":
        return <CheckCircle className="h-3 w-3" />;
      case "closed":
        return <XCircle className="h-3 w-3" />;
      default:
        return <Circle className="h-3 w-3" />;
    }
  };

  if (error) {
    const isAuthError = error.message?.includes("token") || error.message?.includes("authentication") || error.message?.includes("unauthorized");

    return (
      <Page>
        <div className="text-center p-8">
          <div className="h-16 w-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <XCircle className="h-8 w-8 text-red-600" />
          </div>
          <h3 className="text-lg font-medium mb-1">Error loading support tickets</h3>
          <p className="text-gray-500 mb-4">
            {error.message || "Something went wrong"}
          </p>
          <div className="flex flex-col sm:flex-row gap-3 justify-center">
            <button
              className="bg-primary px-4 py-2 rounded-md text-white hover:bg-primary/90 transition-colors"
              onClick={() => refetch()}
              disabled={isPerformingDemoLogin}
            >
              Try Again
            </button>
            
          </div>
          {isAuthError && (
            <p className="text-sm text-gray-400 mt-3">
              It looks like you're not authenticated. Try the demo login to access support tickets.
            </p>
          )}
        </div>
      </Page>
    );
  }

  // Common actions for both list and grid views - Role-based restrictions
  const supportTicketActions: Array<{
    label: string;
    icon: React.ReactNode;
    onClick: (item: SupportTicket) => void;
    variant?: "default" | "destructive";
  }> = [
    {
      label: "View Details",
      icon: <Eye className="h-4 w-4" />,
      onClick: (item: SupportTicket) => {
        navigate(`/support/ticket/${item.ticket_id}`);
      },
    },
    // Only show admin actions to super admins
    ...(isSuperAdmin ? [
      {
        label: "Edit Ticket",
        icon: <Edit className="h-4 w-4" />,
        onClick: (item: SupportTicket) => {
          console.log("Editing ticket", item.ticket_id);
        },
      },
      {
        label: "Delete Ticket",
        icon: <Trash className="h-4 w-4" />,
        onClick: (item: SupportTicket) => handleDeleteTicket(item.ticket_id),
        variant: "destructive" as const,
      },
    ] : []),
  ];

  return (
    <Page>
      <Header
        title={isSuperAdmin ? "Support Center" : "Support Center"}
        buttonText="New Ticket"
        filterByName={true}
        filterWord={debouncedSearch}
        onFilterChange={handleSearchChange}
        useSheet={true}
        sheetContent={<AddSupportTicket onTicketCreated={() => {
          // Reset to page 1 to see the new ticket
          setSearchParams(prev => {
            const newParams = new URLSearchParams(prev);
            newParams.set("page", "1");
            return newParams;
          });
          // Force refetch
          return refetch();
        }} />}
        sheetSize="half"
        viewMode={viewMode}
        onViewModeChange={handleViewModeChange}
        showViewToggle={true}
        showFilters={false} // We'll use our custom filter component instead
      />

      

      <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between mb-4 gap-3">
        <div className="flex items-center space-x-2">
          <Badge variant="secondary" className="px-3 py-1">
            <MessageCircleQuestion className="h-3 w-3 mr-1" />
            <span>{tickets.length} Tickets</span>
          </Badge>
          {activeFilters.status && (
            <Badge variant="outline" className="px-3 py-1">
              Status: {activeFilters.status}
            </Badge>
          )}
          {activeFilters.priority && (
            <Badge variant="outline" className="px-3 py-1">
              Priority: {activeFilters.priority}
            </Badge>
          )}
          {activeFilters.category && (
            <Badge variant="outline" className="px-3 py-1">
              Category: {activeFilters.category}
            </Badge>
          )}
        </div>

        <SupportFilterPopover onFilterChange={handleFilterChange} />
      </div>

      {viewMode === "list" ? (
        <ModernTable<SupportTicket>
          data={tickets}
          isLoading={isLoading}
          isSelectable={true}
          showColumnSelection={true}
          onRowClick={(item) => navigate(`/support/ticket/${item.ticket_id}`)}
          columns={[
            {
              header: "Ticket",
              accessor: "title",
              className: "min-w-[300px]",
              cell: (value: any, item: SupportTicket) => (
                <div className="flex items-center space-x-3">
                  <div className="h-9 w-9 bg-gradient-to-br from-primary/20 to-primary/10 rounded-lg flex items-center justify-center">
                    <MessageCircleQuestion className="h-5 w-5 text-primary" />
                  </div>
                  <div>
                    <div className="font-medium text-gray-900 line-clamp-1">
                      {String(value)}
                    </div>
                    <div className="text-xs text-gray-500">
                      #{String(item.ticket_id).slice(-8).toUpperCase()}
                    </div>
                  </div>
                </div>
              ),
              sortable: true,
            },
            ...(isSuperAdmin ? [{
              header: "User",
              accessor: "user_email" as keyof SupportTicket,
              className: "min-w-[200px]",
              cell: (value: any, item: SupportTicket) => (
                <div className="flex items-center space-x-2">
                  <Avatar className="h-8 w-8">
                    <AvatarFallback className="text-xs">
                      {item.user_first_name?.charAt(0)}{item.user_last_name?.charAt(0)}
                    </AvatarFallback>
                  </Avatar>
                  <div>
                    <div className="font-medium text-gray-900 text-sm">
                      {item.user_first_name} {item.user_last_name}
                    </div>
                    <div className="text-xs text-gray-500">
                      {String(value)}
                    </div>
                  </div>
                </div>
              ),
              sortable: true,
            }] : []),
            {
              header: "Status",
              accessor: "status" as keyof SupportTicket,
              className: "min-w-[120px]",
              cell: (value: any, item: SupportTicket) => (
                <StatusDisplay ticket={item} />
              ),
              sortable: true,
            },
            {
              header: "Priority",
              accessor: "priority" as keyof SupportTicket,
              className: "min-w-[100px]",
              cell: (value: any) => (
                <Badge variant={getPriorityBadgeVariant(String(value))}>
                  {String(value).toUpperCase()}
                </Badge>
              ),
              sortable: true,
            },
            {
              header: "Category",
              accessor: "category" as keyof SupportTicket,
              className: "min-w-[120px]",
              cell: (value: any) => (
                <Badge variant={getCategoryBadgeVariant(String(value))}>
                  {String(value).replace('_', ' ')}
                </Badge>
              ),
              sortable: true,
            },
            {
              header: "Description",
              accessor: "description" as keyof SupportTicket,
              className: "min-w-[200px]",
              cell: (value: any) => (
                <div className="max-w-xs">
                  <span className="text-sm text-gray-600 line-clamp-2">
                    {String(value).length > 100 ? `${String(value).substring(0, 100)}...` : String(value)}
                  </span>
                </div>
              ),
              sortable: true,
            },
            {
              header: "Created",
              accessor: "created_at" as keyof SupportTicket,
              className: "min-w-[120px]",
              cell: (value: any) => (
                <div className="flex items-center space-x-1">
                  <Calendar className="h-4 w-4 text-gray-400" />
                  <span className="text-sm">{formatDate(String(value))}</span>
                </div>
              ),
              sortable: true,
            },
            {
              header: "Updated",
              accessor: "updated_at" as keyof SupportTicket,
              className: "min-w-[120px]",
              cell: (value: any) => (
                <div className="flex items-center space-x-1">
                  <Clock className="h-4 w-4 text-gray-400" />
                  <span className="text-sm">{formatDate(String(value))}</span>
                </div>
              ),
              sortable: true,
            },
          ]}
          actions={supportTicketActions}
          emptyState={
            <div className="text-center p-8">
              <div className="h-16 w-16 bg-primary/10 rounded-full flex items-center justify-center mx-auto mb-4">
                <MessageCircleQuestion className="h-8 w-8 text-primary" />
              </div>
              <h3 className="text-lg font-medium mb-1">No support tickets found</h3>
              <p className="text-gray-500 mb-4">
                Get started by creating your first support ticket
              </p>
              {/* <button
                className="bg-primary px-4 py-2 rounded-md text-white flex items-center justify-center mx-auto"
                onClick={() => console.log("Create support ticket")}
              >
                <Plus className="h-4 w-4 mr-2" />
                Create Ticket
              </button> */}
            </div>
          }
        />
      ) : (
        <SupportTicketGrid
          data={tickets}
          onItemClick={(item) => navigate(`/support/ticket/${item.ticket_id}`)}
          actions={supportTicketActions}
          getStatusBadgeVariant={getStatusBadgeVariant}
          getPriorityBadgeVariant={getPriorityBadgeVariant}
          getCategoryBadgeVariant={getCategoryBadgeVariant}
          getStatusIcon={getStatusIcon}
          formatDate={formatDate}
          isSuperAdmin={isSuperAdmin}
          onStatusUpdate={isSuperAdmin ? handleStatusUpdate : undefined}
          isUpdatingStatus={isSuperAdmin ? updateStatusMutation.isPending : false}
        />
      )}


      {/* Delete Confirmation Dialog */}
      <AlertDialog open={!!deleteTicketId} onOpenChange={() => setDeleteTicketId(null)}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Delete Support Ticket</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to delete this support ticket? This action cannot be undone.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={confirmDelete}
              className="bg-red-600 hover:bg-red-700"
              disabled={deleteMutation.isPending}
            >
              {deleteMutation.isPending ? "Deleting..." : "Delete"}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </Page>
  );
};

export default Support;
