import { useRef, useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  SheetClose,
  SheetTitle,
  SheetDescription,
} from "@/components/ui/sheet";
import {
  MessageCircleQuestion,
  Tag,
  AlertCircle,
  FileText,
  Paperclip,
} from "lucide-react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { useMutation, useQueryClient, useQuery } from "@tanstack/react-query";
import { createSupportTicket, getSupportCategories } from "@/api/services/support/supportService";
import { toast } from "sonner";

// Define the shape of form data
interface FormData {
  title: string;
  description: string;
  category: string;
  priority: string;
  tags?: string[];
}

// Define the validation schema using Yup
const validationSchema = Yup.object().shape({
  title: Yup.string()
    .required("Title is required")
    .min(5, "Title must be at least 5 characters")
    .max(500, "Title cannot exceed 500 characters"),
  description: Yup.string()
    .required("Description is required")
    .min(10, "Description must be at least 10 characters"),
  category: Yup.string().required("Category is required"),
  priority: Yup.string().required("Priority is required"),
  tags: Yup.array().of(Yup.string().required()).optional(),
});

interface AddSupportTicketProps {
  onTicketCreated?: () => void;
}

export function AddSupportTicket({ onTicketCreated }: AddSupportTicketProps = {}) {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const sheetCloseRef = useRef<HTMLButtonElement>(null);
  const [tagInput, setTagInput] = useState("");
  const [tags, setTags] = useState<string[]>([]);
  const queryClient = useQueryClient();

  // Fetch support categories
  const { data: categoriesData } = useQuery({
    queryKey: ["supportCategories"],
    queryFn: getSupportCategories,
  });

  const categories = categoriesData?.data || [];

  // Initialize React Hook Form
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    reset,
    setValue,
    watch,
  } = useForm<FormData>({
    resolver: yupResolver(validationSchema),
    defaultValues: {
      category: "general",
      priority: "medium",
      tags: [],
    },
  });

  // Create ticket mutation
  const createTicketMutation = useMutation({
    mutationFn: createSupportTicket,
    onMutate: async (newTicketData) => {
      console.log("ðŸš€ Starting ticket creation mutation...", newTicketData);

      // Cancel any outgoing refetches (so they don't overwrite our optimistic update)
      await queryClient.cancelQueries({ queryKey: ["supportTickets"] });

      // Snapshot the previous value
      const previousTickets = queryClient.getQueriesData({ queryKey: ["supportTickets"] });

      return { previousTickets };
    },
    onSuccess: async (data, variables) => {
      console.log("âœ… Support ticket created successfully:", data);
      toast.success("Support ticket created successfully!");

      // Call the parent component's refetch function directly
      if (onTicketCreated) {
        console.log("ðŸ”„ Calling onTicketCreated to refetch tickets...");
        try {
          const result = onTicketCreated();
          console.log("âœ… Refetch initiated:", result);
        } catch (error) {
          console.error("âŒ Error refetching tickets:", error);
        }
      } else {
        console.warn("âš ï¸ onTicketCreated callback not provided");
      }

      reset();
      setTags([]);
      setTagInput("");
      // Close the sheet after successful submission
      setTimeout(() => {
        sheetCloseRef.current?.click();
      }, 100);
    },
    onError: (error: any, variables, context) => {
      console.error("âŒ Failed to create support ticket:", error);
      toast.error(error.message || "Failed to create support ticket");

      // Rollback optimistic updates if any
      if (context?.previousTickets) {
        context.previousTickets.forEach(([queryKey, data]) => {
          queryClient.setQueryData(queryKey, data);
        });
      }
    },
    onSettled: () => {
      // Always refetch to ensure consistency
      console.log("ðŸ Mutation settled - clearing cache and refetching queries");

      // Clear all support ticket related cache
      queryClient.removeQueries({
        queryKey: ["supportTickets"],
        exact: false
      });
      queryClient.removeQueries({
        queryKey: ["admin-support-tickets"],
        exact: false
      });

      // Force invalidation and refetch
      queryClient.invalidateQueries({
        queryKey: ["supportTickets"],
        exact: false
      });
      queryClient.invalidateQueries({
        queryKey: ["admin-support-tickets"],
        exact: false
      });

      // Force immediate refetch
      queryClient.refetchQueries({
        queryKey: ["supportTickets"],
        exact: false
      });
    },
  });

  // Handle form submission
  const onSubmit = async (data: FormData) => {
    try {
      const ticketData = {
        ...data,
        tags,
      };

      await createTicketMutation.mutateAsync(ticketData);
    } catch (error) {
      console.error("Error submitting form:", error);
    }
  };

  // Handle tag input
  const handleTagKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" || e.key === ",") {
      e.preventDefault();
      addTag();
    }
  };

  const addTag = () => {
    const trimmedTag = tagInput.trim();
    if (trimmedTag && !tags.includes(trimmedTag)) {
      const newTags = [...tags, trimmedTag];
      setTags(newTags);
      setValue("tags", newTags);
      setTagInput("");
    }
  };

  const removeTag = (tagToRemove: string) => {
    const newTags = tags.filter(tag => tag !== tagToRemove);
    setTags(newTags);
    setValue("tags", newTags);
  };

  // Handle file attachment click
  const handleAttachmentClick = () => {
    fileInputRef.current?.click();
  };

  const priorityOptions = [
    { value: "low", label: "Low", color: "text-green-600" },
    { value: "medium", label: "Medium", color: "text-yellow-600" },
    { value: "high", label: "High", color: "text-orange-600" },
    { value: "urgent", label: "Urgent", color: "text-red-600" },
  ];

  return (
    <div className="space-y-6">
      <div>
        <SheetTitle className="flex items-center space-x-2">
          <MessageCircleQuestion className="h-5 w-5 text-primary" />
          <span>Create Support Ticket</span>
        </SheetTitle>
        <SheetDescription>
          Submit a new support ticket and we'll get back to you as soon as possible.
        </SheetDescription>
      </div>

      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
        <div className="space-y-4">
          <div className="flex items-center space-x-2 mb-2">
            <FileText className="h-5 w-5 text-primary" />
            <h3 className="text-sm font-medium">Ticket Information</h3>
          </div>

          {/* Title Field */}
          <div>
            <Label
              htmlFor="title"
              className="text-sm font-medium text-foreground mb-1.5 block"
            >
              Title<span className="text-red-500">*</span>
            </Label>
            <Input
              id="title"
              placeholder="Brief description of your issue"
              className="w-full border-border/50 bg-card focus:border-primary/50 focus:ring-primary/20"
              {...register("title")}
            />
            {errors.title && (
              <p className="mt-1 text-xs text-red-600">{errors.title.message}</p>
            )}
          </div>

          {/* Description Field */}
          <div>
            <Label
              htmlFor="description"
              className="text-sm font-medium text-foreground mb-1.5 block"
            >
              Description<span className="text-red-500">*</span>
            </Label>
            <Textarea
              id="description"
              placeholder="Please provide detailed information about your issue..."
              rows={6}
              className="w-full border-border/50 bg-card focus:border-primary/50 focus:ring-primary/20 resize-none"
              {...register("description")}
            />
            {errors.description && (
              <p className="mt-1 text-xs text-red-600">{errors.description.message}</p>
            )}
          </div>

          {/* Category Field */}
          <div>
            <Label
              htmlFor="category"
              className="text-sm font-medium text-foreground mb-1.5 block"
            >
              Category<span className="text-red-500">*</span>
            </Label>
            <Select onValueChange={(value) => setValue("category", value)} defaultValue="general">
              <SelectTrigger className="w-full border-border/50 bg-card focus:border-primary/50">
                <SelectValue placeholder="Select a category" />
              </SelectTrigger>
              <SelectContent>
                {categories.map((category: any) => (
                  <SelectItem key={category.category_id} value={category.category_id}>
                    <div className="flex items-center space-x-2">
                      <span>{category.name}</span>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            {errors.category && (
              <p className="mt-1 text-xs text-red-600">{errors.category.message}</p>
            )}
          </div>

          {/* Priority Field */}
          <div>
            <Label
              htmlFor="priority"
              className="text-sm font-medium text-foreground mb-1.5 block"
            >
              Priority<span className="text-red-500">*</span>
            </Label>
            <Select onValueChange={(value) => setValue("priority", value)} defaultValue="medium">
              <SelectTrigger className="w-full border-border/50 bg-card focus:border-primary/50">
                <SelectValue placeholder="Select priority level" />
              </SelectTrigger>
              <SelectContent>
                {priorityOptions.map((option) => (
                  <SelectItem key={option.value} value={option.value}>
                    <div className="flex items-center space-x-2">
                      <AlertCircle className={`h-4 w-4 ${option.color}`} />
                      <span>{option.label}</span>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            {errors.priority && (
              <p className="mt-1 text-xs text-red-600">{errors.priority.message}</p>
            )}
          </div>

          {/* Tags Field */}
          <div>
            <Label
              htmlFor="tags"
              className="text-sm font-medium text-foreground mb-1.5 block"
            >
              Tags (Optional)
            </Label>
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <div className="relative flex-1">
                  <Tag className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                  <Input
                    value={tagInput}
                    onChange={(e) => setTagInput(e.target.value)}
                    onKeyDown={handleTagKeyDown}
                    placeholder="Add tags (press Enter or comma to add)"
                    className="w-full pl-10 border-border/50 bg-card focus:border-primary/50 focus:ring-primary/20"
                  />
                </div>
                <Button
                  type="button"
                  variant="outline"
                  size="sm"
                  onClick={addTag}
                  disabled={!tagInput.trim()}
                >
                  Add
                </Button>
              </div>
              {tags.length > 0 && (
                <div className="flex flex-wrap gap-2">
                  {tags.map((tag, index) => (
                    <span
                      key={index}
                      className="inline-flex items-center px-2 py-1 rounded-md text-xs font-medium bg-primary/10 text-primary border border-primary/20"
                    >
                      {tag}
                      <button
                        type="button"
                        onClick={() => removeTag(tag)}
                        className="ml-1 text-primary/60 hover:text-primary"
                      >
                        Ã—
                      </button>
                    </span>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* File Attachment */}
          <div>
            <Label className="text-sm font-medium text-foreground mb-1.5 block">
              Attachments (Optional)
            </Label>
            <div
              onClick={handleAttachmentClick}
              className="border-2 border-dashed border-border/50 rounded-lg p-4 text-center cursor-pointer hover:border-primary/50 transition-colors"
            >
              <Paperclip className="h-8 w-8 text-muted-foreground mx-auto mb-2" />
              <p className="text-sm text-muted-foreground">
                Click to attach files or drag and drop
              </p>
              <p className="text-xs text-muted-foreground mt-1">
                Supported formats: PDF, DOC, DOCX, TXT, PNG, JPG (Max 10MB)
              </p>
            </div>
            <input
              ref={fileInputRef}
              type="file"
              multiple
              accept=".pdf,.doc,.docx,.txt,.png,.jpg,.jpeg"
              className="hidden"
              onChange={(e) => {
                // Handle file upload logic here
                console.log("Files selected:", e.target.files);
              }}
            />
          </div>
        </div>

        {/* Form Actions */}
        <div className="flex items-center justify-end space-x-3 pt-4 border-t border-border/50">
          <SheetClose asChild>
            <Button
              ref={sheetCloseRef}
              type="button"
              variant="outline"
              className="border-border/50 hover:bg-secondary/80"
            >
              Cancel
            </Button>
          </SheetClose>
          <Button
            type="submit"
            disabled={isSubmitting || createTicketMutation.isPending}
            className="bg-primary hover:bg-primary/90 text-primary-foreground"
          >
            {isSubmitting || createTicketMutation.isPending ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                Creating...
              </>
            ) : (
              <>
                <MessageCircleQuestion className="h-4 w-4 mr-2" />
                Create Ticket
              </>
            )}
          </Button>
        </div>
      </form>
    </div>
  );
}
